/* Sidebar.css */
.sidebar {
  height: 100vh;
  color: white;
  padding: 10px;
  flex: 0.2;
  min-width: 200px;
  background-color: #040404;
}

.sidebar > hr {
  border: 1px solid #282828;
  width: 90%;
  margin: 10px auto;
}

.sidebar_logo {
  height: 70px;
  width: auto;
  padding: 10px;
  margin: auto;
}

.sidebar_title {
  margin-left: 5px;
  padding: 5px;
  font-size: 12px;
}
